@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
html{
  background-color: rgba(27, 233, 147, 0.89);
  background-attachment: fixed;
  font-family: 'Balsamiq Sans', cursive;
}


.card{
  position: absolute;
  top: 0; 
  bottom: 0;
  left: 0; 
  right: 0;
  margin: auto;

  background-color: whitesmoke;
  border-radius: 10px;
  width: 50vw;
  height: 300px;
  box-shadow: 0 0 2em rgb(0, 0, 0);
  padding: 20px;

}

.positive-button{
  text-decoration: none;
  color: white;
  background-color: green;
  padding: 5px 25px ;
  border-radius: 10px;
}

.negative-button{
  color: rgb(223, 3, 3);
}
.home-page{text-align:center}.home-page .card{font-size:20px;display:flex;flex-direction:column;align-items:center}.home-page .card .home-input{margin-top:10%;text-align:center}.home-page .card .positive-button{width:100px;justify-self:end;margin-top:20px}@media screen and (max-width: 500px){.home-page .card{font-size:20px;width:80vw}}

.quest-card{font-size:20px;background-color:rgba(245,245,245,.719);margin:15px 10%;padding:40px;font-family:"Roboto","Helvetica","Arial",sans-serif;font-weight:400;font-size:1.4rem}.quest-card .info-quest-card .difficulty{float:right;padding:10px;margin:10px;border:5px solid rgba(27,233,147,.89);border-radius:30px;font-size:1.4rem;text-transform:uppercase;font-weight:bolder;text-align:right}@media only screen and (max-width: 500px){.quest-card{margin:5px 0%;font-size:1rem}}



.card{font-size:25px;text-align:center}.card .confirm-page-buttons{margin-top:15%;text-align:center}.card .confirm-page-buttons :nth-child(n){margin:20px}
